import { Component, OnInit } from '@angular/core';
import { Router, NavigationEnd, ActivatedRoute } from '@angular/router';
import { CookieService } from 'ngx-cookie';
import { SharedService } from '../../shared/services/shared.service';
@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {

  token        = '';
  user         = {};
  cities       = [];
  Items        = [];
  showCities:boolean = false;
  showItems:boolean = false;
  city         :any;
  searchKeyword:any;
  selectedCity:any;
  constructor(
    private _router : Router,
    private _sharedService:SharedService,
    private _cookieService: CookieService) { }

  ngOnInit() {

  	this._router.events.subscribe((evt) => {
            if (!(evt instanceof NavigationEnd)) {
	        return;
	    }
	    this.token = this._cookieService.get('token');
	    this.user = this._cookieService.getObject('user') ?  this._cookieService.getObject('user') : {};
	    // window.scrollTo(0, 0)
	});
  	this.token = this._cookieService.get('token');
            this.user = this._cookieService.getObject('user') ?  this._cookieService.getObject('user') : {};
  }

  logout() {
  	this.token = '';
  	this.user = {};
  	this._cookieService.removeAll();
    this._sharedService.showAlert('Logout Sucessfully','alert-success');
    this._router.navigate(['/auth/login-signup']);

  }

  searchCity() {
      let options = {
        city:this.city
      }
      this._sharedService.queryParams('searchCity',options).then((res: any) => {
          if(res.success) {
              this.cities = res.data || []; 
          } else { }
      },(error) => { });
  }

  searchProduct() {
      console.log("yssss")
      let data = {
        name:this.searchKeyword,
        city:this.selectedCity
      }
      this._sharedService.post(data,'Search').then((res: any) => {
          if(res.success) {
              this.Items = res.data.Item || []; 
          } else { }
      },(error) => { });
  }    
}



